export default {
    props: {
        apiUrl: {
            type: String,
            required: true
        },
        size: {
            default: "sm",
            type: String
        },
        placeholder: {
            type: String,
            default: "Search..."
        },
        debounce: {
            type: String | Number,
            default: 300
        },
        value: {
            default: null
        },
        valueField: {
            type: String,
            default: 'value'
        },
        identifier: {
            type: String,
            default: 'id'
        }
    },
    data() {
        return {
            items: [],
            filter: null,
            query: null,
            selected: null
        }
    },
    watch: {
        query() {
            this.fetchData();
        },
        value: {
            deep: true,
            handler(v) {
                if (this.valueField && v) {
                    this.selected = (this.items || []).find(i => i.value === v);
                } else {
                    this.selected = v;
                }
            }
        }
    },
    mounted() {
        this.init();
    },
    methods: {
        init() {
            if (this.value) {
                let q = {key: this.identifier, value: this.value};
                let the = this;
                this.fetchData(q, function (items) {
                    the.selected = (items || []).find(i => i.value === q.value);
                });
            }
        },
        selectItem(item) {
            this.$set(this, 'selected', item);
            this.$emit("input", (this.selected && this.valueField) ? this.selected[this.valueField] : this.selected);
        },
        fetchData(q = null, callback = null) {
            this
                .$axios
                .post(this.apiUrl, q || {filter: this.query})
                .then(res => {
                    this.items = res.data;
                    if (typeof callback === "function") {
                        callback(this.items);
                    }
                })
                .catch(err => {
                    console.log(err.response);
                });
        }
    }
}
